$main-size: 1200px;
$dark-grey: #2e2e2e;
$almost-white: #e0e0e0;

@font-face {
  font-family: "Futura";
  src: url("../fonts/futura medium condensed bt.ttf")
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway/Raleway-VariableFont_wght.ttf ")
}

//@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Victor+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');

@font-face {
  font-family: "SourceCode";
  src: url("../fonts/Source_Code_Pro/SourceCodePro-VariableFont_wght.ttf")
}

h2 {
	text-transform:uppercase;
	margin: 10px 0px 5px 0px;
}

a, .clickable {
	color: black;
	text-decoration:underline;
	cursor: pointer;
}

h3 {
	font-weight: 900;
	margin: 5px 0px;
}

a:visited{
	color: rgb(0,0,0)
}

a:hover, .clickable:hover{
	color: rgb(230,230,230)
}

header, body, .content {
	margin:0;
	padding:0;
	width:100%;
	color: $almost-white;
	font-family: "Futura";
}

/*.content {
	//background-image: url("../images/lzs_07_2x.jpg");
	//color:black;
}*/

body {
	background-color:black;
}

header h1 {
	font-size:48px;
  display: inline;
  margin:0px;
  margin-left:100px;
}

header {
	padding-top: 100px;
	border-bottom: 15px solid $dark-grey;
}

header a, header a:visited {
	text-decoration:none;
	color: $almost-white;
}

#cuteline {
	position: absolute;
	z-index: -4;
	top: 0px;
}

#title-container {
	max-width: $main-size;
    margin: 0 auto;
}

nav{
	font-size:30px;
	text-decoration:none;
    height: 50px;
    margin: 0 auto;
    max-width: 400px;
    text-align: right;
}

nav a{
	text-decoration:none;
	padding:0 20px;
}

.content-sec {
	//font-family: 'Raleway', Sans-serif;
    font-size: 25px;
    font-weight: 300;
	position: relative;

    padding: 5px 0px;
    max-width: 100%;
	margin: 0 auto;
}

.content-sec.sec-crr{
	color: black;
}

.content-sec.sec-crr:before{
	content: "";
	position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
	box-shadow: inset black 0px 0px 8px 1px;
	background-color: rgba(255,255,255,1);
    -webkit-mask-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 800 800'%0Axmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeGaussianBlur in='SourceAlpha' stdDeviation='2'/%3E%3CfeTurbulence type='fractalNoise' baseFrequency ='0.6' stitchTiles='stitch'%0AnumOctaves='6'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E"), linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    -webkit-mask-repeat: repeat;
    -webkit-mask-size: 185px;
    -mask-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 800 800'%0Axmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeGaussianBlur in='SourceAlpha' stdDeviation='2'/%3E%3CfeTurbulence type='fractalNoise' baseFrequency ='0.6' stitchTiles='stitch'%0AnumOctaves='6'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E"), linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    -mask-repeat: repeat;
    -mask-size: 185px;
}

.content-sec.sec-dark {
	background-color: $dark-grey;
}

.content-sec.sec-dark a, .content-sec.sec-dark {
	color: $almost-white;
}

.sec-container {
	max-width: $main-size;
	margin: 0 auto;
	padding: 0 10px;
}

.sec-container p{
	max-width: $main-size;
	//margin: 0 auto;
	padding: 0 10px;
} 

.content-sec ul.postlist{
	padding: 0px;
	margin:	0px;
}

.content-sec ul li.postlist-li{
	padding: 10px 20px;
	margin:	0px;
	list-style: None;
//	background: rgb(255 255 255 / 47%);
//	border: $dark-grey solid 5px;
  margin: 10px 0px;
}

.activity-type, .activity-content {
	display:inline-block;
	vertical-align: middle;
	margin: 0px;
}

.activity-type {
	width:100px;
}

.activity-dateduration, .activity-title, .activity-location, .activity-content {
	margin: 0px;
	padding:0px;
}

.activity-content {
	  border-left: 1px solid black;
    padding-left: 10px;
    max-width: $main-size - 200px;
}

img.project-img{
	display: block;
    max-width: 1000px;
    margin: 0 auto;

	//box-shadow: #48484894 0px 0px 20px 0px;
	filter: drop-shadow(0px 0px 20px rgba(72,72,72,0.6));
}

@media (max-width: $main-size) {
img.project-img{
    max-width: 100%;
}
}
/* Grid project display */
ul.cool-wrapper {
display: flex;
    align-content: center;
    justify-content: center;
    justify-items: stretch;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    padding: 0;
    margin: 0;
}

li.cool-bloc {
    list-style: none;
    flex-basis: fit-content;
    max-width: 100%;
}

li.cool-bloc a {
	  position: relative;
    display: block;
    padding: 0 3px;
}

li.cool-bloc img{
	//box-shadow: rgba(72, 72, 72, 0.5803921569) 0px 0px 20px 0px;
	filter: drop-shadow(0px 0px 20px rgba(72,72,72,0.6));
    position: relative;
    max-height: 280px;
    max-width: 100%;
}

li.cool-bloc h2{
display: block;
    position: absolute;
    z-index: 3;
    top: 50%;
    left: 0;
    font-size: 44px;
    color: white;
    width: 100%;
    text-align: center;
    transform: translate(0%, -50%);
    mix-blend-mode: difference;
    text-shadow: 2px 1px 0px black;
}

li.cool-bloc:hover h2{
	mix-blend-mode: luminosity;
	transition:
	    mix-blend-mode 2s,
}